<template>
    <div class="row no-gutters wrapper">
        <Navigation></Navigation>
        <div class="col-md-10 main-content">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-9">

                        <div class="page-header">
                            <h1>Tags</h1>
                        </div>
                        <div class="list-group list-group-flush">
                            <a href="#" v-for="(tag, index) in tags" v-on:click.prevent="selectTag(index)" class="list-group-item">
                                <div class="justify-content-between">
                                    <h5 class="mb-1">{{ tag.title }}</h5>
                                </div>
                                <small>{{ tag.content }}</small>
                            </a>
                        </div>

                    </div>

                    <div class="col-md-3">
                        <div class="card" v-if="selected_tag !== null">
                            <div class="card-body">
                                <h4 class="card-title">Edit Tag</h4>
                                <form v-on:submit.prevent="edit_tag()">
                                    <div class="alert alert-success text-center" v-show="alerts.edit.success"><i class="far fa-thumbs-up"></i></div>
                                    <div class="alert alert-danger text-center" v-show="alerts.edit.error">Error updating user.</div>
                                    <div class="form-group">
                                        <input type="text" v-model="selected_tag.title" placeholder="Title" class="form-control" />
                                    </div>
                                    <div class="form-group">
                                        <textarea v-model="selected_tag.content" placeholder="Description" class="form-control"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-success">Edit</button>
                                        <button type="button" class="btn btn-danger" v-on:click.prevent="delete_tag()">Delete</button>
                                    </div>
                                </form>
                            </div>
                        </div>

                        <div class="card">
                            <div class="card-body">
                                <h4 class="card-title">New Tag</h4>
                                <form v-on:submit.prevent="create_tag()">
                                    <div class="alert alert-success text-center" v-show="alerts.new.success"><i class="far fa-thumbs-up"></i></div>
                                    <div class="alert alert-danger text-center" v-show="alerts.new.error">Error creating user.</div>
                                    <div class="form-group">
                                        <input type="text" v-model="new_tag.title" placeholder="Title" class="form-control" />
                                    </div>
                                    <div class="form-group">
                                        <textarea v-model="new_tag.content" placeholder="Description" class="form-control"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-primary">Create</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Navigation from '../Navigation.vue'

export default {
  name: 'TagsList',
  components: {
      Navigation
  },
  created() {
      this.getTags()
  },
  data () {
    return {
        tags: [],
        selected_tag: null,
        selected_index: null,
        new_tag: {
            username: null,
            password: null,
            email: null
        },
        alerts: {
            edit: {
                success: false,
                error: false,
            },
            new: {
                success: false,
                error: false,
            }
        }
    }
  },
  methods: {
      getTags() {
          this.axios.get(this.api + '/tags').then((res) => {
              this.tags = res.data
          })
          .catch((err) => {
              console.log(err);
          });
      },
      selectTag(index) {
          this.selected_tag = this.tags[index]
          this.selected_index = index
      },
      create_tag() {
          this.alerts.new.success = false
          this.alerts.new.error = false
          this.axios.post(this.api + '/tags', this.new_tag).then((res) => {
              if (res.data.error == 0){
                  this.alerts.new.success = true;

                  this.tags.push(res.data.tag)
              }
              else
              {
                  this.alerts.new.error = true;
              }
          })
          .catch((err) => {
              console.log(err)
          })
      },
      edit_tag() {
          this.alerts.edit.success = false
          this.alerts.edit.error = false
          this.axios.put(this.api + '/tags/' + this.selected_tag._id, { title: this.selected_tag.title, content: this.selected_tag.content }).then((res) => {
              if (res.data.error == 0){
                  this.alerts.edit.success = true
                  //Change username in array from index.
                  this.tags[this.selected_index].title = this.selected_tag.title
                  this.tags[this.selected_index].content = this.selected_tag.content
              }
              else
              {
                  this.alerts.edit.error = true;
              }
          })
          .catch((err) => {
              console.log(err)
          })
      },
      delete_tag() {
          this.axios.post(this.api + '/tags/delete/' + this.selected_tag._id).then((res) => {
              if (res.data.error == 0){
                  this.tags.splice(this.selected_index, 1)

                  // Clear variables.
                  this.selected_tag = null
                  this.selected_index = null
              }
          })
          .catch((err) => {
              console.log(err)
          })
      }
  }
}
</script>



